// è‹¥æžœè¦æŒ‚è½½åŽŸåž‹ app.config.globalProperties
import defaultImg from '@/assets/image/loding.svg'

export default {
    install(app) {
        auto()
        //å›¾ç‰‡æ‡’åŠ è½½æŒ‡ä»¤
        defineDirective(app)
    }
}

//è‡ªå®šä¹‰ æŒ‡ä»¤
const defineDirective = (app) => {
    app.directive('lazy',{
        mounted(el,binding) {
            const observe = new IntersectionObserver(([{isIntersecting}]) => {
                if(isIntersecting) {
                    observe.unobserve(el)
                    if(binding.value) {
                        el.src = binding.value
                        console.log('å›¾ç‰‡åŠ è½½æˆåŠŸ')
                    }
                    if(!binding.value) {
                        el.src = defaultImg
                        console.log('å›¾ç‰‡åŠ è½½å¤±è´¥')
                    }
                }
            },{threshold:0.01})
            observe.observe(el)
        },
    })
}

//è®¾ç½®è·Ÿå­—ä½“å¤§å°
function auto() {
    let clientWidth = document.documentElement.clientWidth
    let rootFS = clientWidth * 100 / 375
    document.documentElement.style.fontSize = rootFS + 'px'
    console.log('auto ')
}
window.onresize = auto